import React from 'react';
import './App.css';
import HeaderContainer from './Components/Header/HeaderContainer';
import Navbar from './Components/Navbar/Navbar';
import UsersContainer from './Components/users/UsersContainer'
import { Redirect, Route, Switch } from 'react-router-dom'
import Login from './Components/Login/Login'
import { InitializeApp, showGlobalError } from './redux/appReducer'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { compose } from 'redux'
import Preloader from './Components/common/Preloader/Preloader';
import store from './redux/reduxStore'
import { HashRouter } from 'react-router-dom';
import { Provider } from 'react-redux'
import { WithSuspense } from './hoc/WithSuspens';

const DialogsContainer = React.lazy(() => import('./Components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() => import('./Components/Profile/ProfileContainer'))

class App extends React.Component {

  catchAllUnhandledErrors = (promiseRejectionEvent) => {
    promiseRejectionEvent()
  }

  componentDidMount() {
    this.props.InitializeApp()
    window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors(showGlobalError))
  } //Посмотри про эту хрень на видео повнимательнее и пошли уже делать thunk и 2 Action Creator ( in value, in null)
  //Типа диспатчим и обнуляем ошибку, а thunk для setTimeout, чтобы потом убрать ошибку
  componentWillUnmount() {
    window.removeEventListener('unhandledrejection', this.catchAllUnhandledErrors(showGlobalError))
  }

  render() {
    if (!this.props.initialized) {
      return <Preloader />
    }

    return (
      <div className='app-wrapper'>
        <HeaderContainer />
        <Navbar />
        <div className='app-wrapper-content'>
          <Switch>

            <Route exact path='/' render={() => <Redirect from="/" to="/profile" />} />

            <Route path='/dialogs' render={WithSuspense(DialogsContainer)} />

            <Route path='/profile/:userId?' render={WithSuspense(ProfileContainer)} />

            <Route path='/users' render={() => <UsersContainer />} />

            <Route path='/login' render={() => <Login />} />

            <Route path='*' render={() => <div>404 NOT FOUND</div>} />
          </Switch>
        </div>
      </div>
    )
  }
}

let mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

let AppContainer = compose(
  connect(mapStateToProps, { InitializeApp }),
  withRouter
)(App)

const MainApp = (props) => {
  return <HashRouter>
    <Provider store={store}>
      <AppContainer />
    </Provider>
  </HashRouter>
}

export default MainApp